.our-poeple{
    font-family: Georgia, 'Times New Roman', Times, serif;
    background-color: gold;
}
.our-poeple-title{
    text-align: center;
    background-color: #ccf6f1;
    height: 175px;
    padding : 40px;

}
.our-poeple-title h2{
